import React from 'react';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Chip from '@material-ui/core/Chip';
import { Avatar } from '@material-ui/core';
import Paper from '@material-ui/core/Paper';


const AboutText = "I am currently a student at the University of Texas at Austin studying Computer Engineering. My focus areas are Software Engineering and Data Science.";


const About = () => {
    return (
        <React.Fragment>
            <Container maxWidth = "xl">
                <Grid container spacing = {3} direction = "column" alignItems = "center" justify = "center" style = {{ minHeight: '100vh' }}>
                    <Grid item xs = {12}>
                        <Typography variant = "h2">About Me</Typography>
                    </Grid>
                    <Grid item xs = {3}>
                        <Typography align = "center" variant="body1">{AboutText}</Typography>
                    </Grid>
                    <Grid item xs = {12}>
                        <Typography variant = "h2">Skills</Typography>
                    </Grid>
                    <Grid item xs = {3}>
                        <Typography align = "center" variant="body1">I am proficient at <b>C and C++</b> for embedded applications, the <b>MERN stack</b> for web dev, and <b>.NET development</b>, including <b>SQL Server.</b></Typography>
                    </Grid>
                    <Grid item xs = {3}>
                        <Typography align = "center" variant="body1">I also have experience writing programs in <b>Python</b> and <b>Java</b>. Finally, I have low-level experience in <b>x86 Assembly</b> and <b>ARM Assembly</b>.</Typography>
                    </Grid>
                </Grid>
            </Container>
        </React.Fragment>
    )
}

export default About;